@import url(https://fonts.googleapis.com/css?family=Raleway:300,400,500,700&display=swap);

body {
  font-family: "Raleway";
  margin: 0;
  overflow-x: hidden;
  letter-spacing: 0.02em;
}

.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 140%;
  background-color: white;
  background-color: var(--background-color);
}

.activeLink {
  font-weight: bolder;
  color: rgb(5, 134, 255);
}

.dark {
  --background-color: #303030;
  --heading-color: rgba(255, 255, 255, 0.7);
  --background-color-secondary: #424242;
}

:root {
  --background-color-secondary: rgba(219, 219, 219, 0.383);
  --background-color: white;
}

.link {
  text-decoration: none;
  color: inherit;
  font-weight: bolder;
}

.link:hover {
  color: rgb(5, 134, 255);
  cursor: pointer;
  padding-bottom: 2px;
  border-bottom: 3px solid rgb(5, 134, 255);
}

.outboundLink {
  color: rgb(5, 134, 255);
  text-decoration: none;
}

.content {
  margin: 2% 0% 2% 0%;
}

.fa {
  color: gray;
  font-size: 100%;
}

.fa:hover {
  color: #0586ff;
}

.heading {
  font-weight: bolder;
  font-size: 120%;
  color: var(--heading-color);
}

.heading2 {
  font-size: 100%;
  font-weight: normal;
}

.signature {
  float: right;
  font-size: 90%;
  padding: 5%;
}

.sub-content {
  color: gray;
  font-size: 70%;
}

.main-content {
  margin: 2%;
}

hr {
  width: 10%;
}

.skillDiv {
  background-color: rgba(219, 219, 219, 0.383);
  background-color: var(--background-color-secondary);
}

.sectionTitle {
  text-align: center;
}

.white {
  color: white;
}

.blue {
  color: #0586ff;
}

.navbar {
  padding: 10vh 0 10vh 0;
}

.switchThemeButton:hover {
  cursor: pointer;
}

.bottomNav {
  padding: 10vh 0vh 7vh 0;
}

@media only screen and (orientation: landscape) {
  .navbar {
    padding: 5vw 0 5vw 0;
  }
}

.profilePic{
    max-height: 45vh;
    max-width: 45vw; 
}

.typingEffect {
    font-size: 150%;
    align-items: center;
    color: rgb(5, 134, 255);
    font-weight: bolder;
}
.xpDiv {
  background-color: #0586ff;
  padding: 13vh 0 15vh 0;
}

.xpItem {
  margin-bottom: 2%;
  margin-top: 2%;
}

.xpPaper {
  padding: 6.5%;
}

.xpLogo {
  float: left;
  width: 100%;
  max-width: 100px;
  height: auto;
  padding: 0 6.5% 6.5% 0;
}

.xpDescription {
  color: gray;
  font-size: 63%;
  margin-top: 1%;
}

@media only screen and (orientation: landscape) {
  .xpDiv {
    padding: 8vw 0 8vw 0;
  }
}

.eduItem {
  margin-bottom: 3vh;
}

.eduPaper {
  padding: 6.5%;
}

.eduDescription {
  color: gray;
  font-size: 60%;
  margin-top: 1%;
}

.educationContainer {
  padding: 13vh 0 13vh 0;
}

@media only screen and (orientation: landscape) {
  .educationContainer {
    padding: 4vw 0 5vw 0;
  }
}

.skillChip {
  margin-top: 3%;
}

.shadow {
  box-shadow: 0 8px 12px 0 rgba(0, 0, 0, 0.2), 0 10px 25px 0 rgba(0, 0, 0, 0.19);
}

.skillDiv {
  padding: 13vh 0 13vh 0;
}

@media only screen and (orientation: landscape) {
  .skillDiv {
    padding: 6vw 0 8vw 0;
  }
}

.input {
  width: 100%;
}
.successMessage {
  font-weight: bolder;
  font-size: 120%;
  color: inherit;
}

.contactContainer {
  padding: 13vh 0 13vh 0;
}

@media only screen and (orientation: landscape) {
  .contactContainer {
    padding: 5vw 0 5vw 0;
  }
}

